/* --- */
/* Initialize Mixins (enable/disable features) */
/* +++ */

//##! Constrain Boxes Properties
//+++

//## Normal Box Width
$constrainWidth: 1024px;
//## Large Box Width
$largeWidth: 1200px;
//## Small Box Width
$smallWidth: 700px;

//---
//### Constrain Boxes
//+++

//## Normal box ,constrain box
@include box-normal{
    max-width: $constrainWidth;
};
//## Large box
@include box-large{
    max-width: $largeWidth;
};
//## Fluid box ,full box
@include box-fluid;
//## Justify box, stretch box
@include box-justify;
//## Small box
@include box-small{
    max-width: $smallWidth;
};
//## Extended box
@include box-extended;

